              showProvince(provincesList[key], key);
              break;
            }
          }
        } else if (param.name in citysList) {
          // 处理市模块
          let names = param.name;
          console.log(names);
          for (let key in citysList) {
            if (names == key) {
              showCitys(citysList[key], key);
              break;
            }
          }
        } else if (param.name in Mapian) {
          // 处理市模块
          let names = param.name;
          console.log(names);
          for (let key in Mapian) {
            if (names == key) {
              showC(Mapian[key], key);
              break;
            }
          }
        } else if (param.name in townList) {
          // 处理市模块
          let names = param.name;
          console.log(names);
          for (let key in townList) {
            if (names == key) {
              showTown(townList[key], key);
              break;
            }
          }
        }
      });
      //展示对应的省
      function showProvince(eName, param) {
        console.log(eName, param);
        //jquery方式
        // $.getJSON(`/map/province/${eName}.json`, data => {
        //   echarts.registerMap(param, data);
        //   initEcharts(param);
        // });
        //axios方式
        axios
          .get(`/map/province/${eName}.json`)
          .then(res => {
            echarts.registerMap(param, res.data);
            initEcharts(param);
          })
          .catch(error => {
            console.error("出错:", error);
          });
      }
      function showCitys(cName, param) {
        console.log(cName, param);
        axios
          .get(`/map/city/${cName}.json`)
          .then(res => {
            echarts.registerMap(param, res.data);
            initEcharts(param);
          })
          .catch(error => {
            console.error("出错:", error);
          });
      }
      
      function showC(cName, param) {
        console.log(cName, param);
        axios
          .get(`/map/county/${cName}.json`)
          .then(res => {
            echarts.registerMap(param, res.data);
            initEcharts(param);
          })
          .catch(error => {
            console.error("出错:", error);
          });
      }

      function showTown(cName, param) {
        console.log(cName, param);
        axios
          .get(`/map/town/${cName}.json`)
          .then(res => {
            echarts.registerMap(param, res.data);
            initEcharts(param);
          })
          .catch(error => {
            console.error("出错:", error);
          });
      }
    }
  }
};
</script>

